$( ".slick-slider" ).each(function(index) {
    $('.big-slider', $(this)).slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        fade: true,
        asNavFor: $(this).find('.small-slider')
    });
    $('.small-slider', $(this)).slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        // dots: true,
        centerMode: true,
        focusOnSelect: true,
        asNavFor: $(this).find('.big-slider'),
        responsive: [
            {
                breakpoint: 768,
                settings: {
                    slidesToShow: 2,
                }
            },
        ]
    });
});

$('.advantages2__slider').slick({
    slidesToShow: 3,
    slidesToScroll: 1,
    dots: true,
    responsive: [
        {
            breakpoint: 768,
            settings: {
                slidesToShow: 2,
            }
        },
        {
            breakpoint: 500,
            settings: {
                slidesToShow: 1,
            }
        },
    ]
});

$('.videos-big').slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    fade: true,
    asNavFor: '.videos-small__wrap'
});
$('.videos-small__wrap').slick({
    slidesToShow: 5,
    slidesToScroll: 1,
    asNavFor: '.videos-big',
    dots: true,
    focusOnSelect: true,
    responsive: [
        {
            breakpoint: 1500,
            settings: {
                slidesToShow: 4,
            }
        },
        {
            breakpoint: 992,
            settings: {
                slidesToShow: 3,
            }
        },
        {
            breakpoint: 768,
            settings: {
                slidesToShow: 2,
            }
        },
    ]
});


$(document).ready(function() {


    //Menu
    $('.burger').click(function () {
        $('.header__right').addClass('header__right--active')
        $('.menu-bg').addClass('menu-bg--active')
        $('body').addClass('fixed-body')
    })

    $('.close-menu').click(function () {
        $('.header__right').removeClass('header__right--active')
        $('.menu-bg').removeClass('menu-bg--active')
        $('body').removeClass('fixed-body')
    })

    //Filter
    $('.works__filter').click(function () {
        $('.works__top').addClass('works__top--active')
        $('.menu-bg').addClass('menu-bg--active')
        $('body').addClass('fixed-body')
    })

    $('.close-menu').click(function () {
        $('.works__top').removeClass('works__top--active')
        $('.menu-bg').removeClass('menu-bg--active')
        $('body').removeClass('fixed-body')
    })

    //Select
    $('.select__list-default').hover(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list .select__check').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list .select__check").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list .select__check')
    let selectWrap = $('.select__wrap')

    $(document).mouseover(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

    //SlickPagination
    // var $status = $('.pagingInfo');
    // var $slickElement = $('.small-slider');
    //
    // $slickElement.on('init reInit afterChange', function (event, slick, currentSlide, nextSlide) {
    //     var i = (currentSlide ? currentSlide : 0) + 1;
    //     $status.text(i + '/' + slick.slideCount);
    // });



})